<template>
  <div class="feedback-wizard-wrapper">
    <div class="mainContent" v-if="errorReport == false">
<!-------------------------- 
      STEP 1: Problem Description 
---------------------------->
  <wizard-step
          :title="title1"
          :subTitle="subTitle1"            
          :step="step1"
          :stepCount="stepCount3"
          :completed="isStep1Completed">
          <textarea id="errorDescription" rows="10" cols="70" placeholder="Type the problem's description here" v-model="problemDescription.detail" />      
          <p v-if="problemDescription.detail.length > 50 "></p>
          <p class="problemDescriptionCounter" v-else-if="problemDescription.detail.length > 1">
            The problem description has to contain at least 50 characters.
          </p>            
  </wizard-step>
<!-------------------------- 
      STEP 2: QUESTIONS 
---------------------------->
  <wizard-step
        :title="title2"
        :subTitle="subTitle2"
        :step="step2"
        :stepCount="stepCount3"               
        :completed="isStep2Completed"
        v-if="isStep1Completed == true">
    <question 
            :title="questionTitle1"
            :step="questionStep1"
            :completed="isQuestion1Completed">
            <date-picker v-model="questionOne.detail" type="datetime"></date-picker>
            <div class="btn" @click="activate(1), isQuestion1Completed = true" :class="{ active : active_el == 1 }" v-if="questionOne.detail.length != null">I don't remember</div>
    </question>

    <question 
            :title="questionTitle2"
            :step="questionStep2"
            :completed="isQuestion2Completed"
            v-if="isQuestion1Completed == true">    
 
          <div class="buttons">
            <div class="btn" @click="questionStep4 = 4, activate(2)" :class="{ active : active_el == 2 }">Yes</div>
            <div class="btn" @click="questionStep4 = 3, activate(3)" :class="{ active : active_el == 3 }">No</div>
          </div>                                                             
    </question>

    <question 
            :title="questionTitle3"
            :step="questionStep3"
            :completed="isQuestion3Completed"
            v-if="active_el == 2">                               
            <input class="longerInput" type="text" v-model="questionThree.detail"/>                       
    </question>    

    <question 
            :title="questionTitle4"
            :step="questionStep4"                  
            :completed="isQuestion4Completed"
            v-if="active_el == 3 || isQuestion3Completed == true">                
            <input type="text" class="longerInput" v-model="questionFour.detail"/>                       
    </question>  
  </wizard-step>  
<!-------------------------- 
      STEP 3: DATABASE 
---------------------------->  
  <wizard-step
          :title="title3"
          :subTitle="subTitle3"
          :step="step3"             
          :stepCount="stepCount3"          
          :completed="isStep3Completed"
          v-if="isStep2Completed == true">  
    <Solutions>

        <div class="buttons">
          <div class="btn" @click.once="activate(4), stepCount3 = 6, isStep3Completed = true;" :class="{ active : active_el == 4 }">Yes, I give my permission</div>
          <div class="btn" @click="activate(5), stepCount3 = 6, isStep3Completed = true;" :class="{ active : active_el == 5 }">No</div>
        </div>
    </Solutions>
  </wizard-step>
<!-------------------------- 
      STEP 4: Screenshots 
---------------------------->
   <wizard-step
          :title="title4"
          :subTitle="subTitle4"
          :step="step4"            
          :stepCount="stepCount3"          
          :completed="isStep4Completed"
          v-if="isStep3Completed == true">

          <Screenshot>
                <div class="uploadBtn" @click.once="uploadImg()">
                  Upload
                </div>
            </Screenshot>
            <div class="btn" @click="delayStepFive(), isQuestion4Completed = true, activate(6)" :class="{ active : active_el == 6 }">I would like to skip this step</div>     
  </wizard-step>
<!-------------------------- 
      STEP 5: CONFIRM 
---------------------------->  
  <wizard-step id="summary"
          :title="title5"
          :subTitle="subTitle5"
          :step="step5"            
          :stepCount="stepCount3"          
          :completed="isStep5Completed"
          v-if="isStep4Completed == true">
          <p> A summary of the described problem as it's going to be reported. Can you please review the information? You can make any adjustment.</p>            
          <div class="confirmedEl" v-for="item in form" :key="item.name" >
            
            <div v-if="item.detail != ''" class="confirmedElContent">
            <span class="icon">
                <img src="@/assets/elipse.png" width="20"/> 
              </span>            
              <div class="elDescription" >
                <span class="elTitle">
                    {{ item.name }}
                </span>      
                <span class="elContentPlace" v-show="!item.edit">{{item.detail}}</span>

        <input id="elContent" type="text"
              ref="input"
              v-model="item.detail"
              v-show="item.edit"
              v-on:blur="saveEdit(index, item)">
              </div>
            <span class="icon pencil" v-on:click="toggleEdit(index, item)">
              <img src="@/assets/edit.png" width="40" v-show="!item.edit" /> 
            </span>
          </div>     
        </div>
  </wizard-step>
  <!-------------------------- 
      STEP 6: FEEDBACK 
---------------------------->  
  <wizard-step
          :title="title6"
          :subTitle="subTitle6"            
          :step="step6"            
          :stepCount="stepCount3"          
          :completed="isStep6Completed"
          v-if="isStep5Completed == true">
          <textarea id="feedbackDescription" rows="10" cols="70" placeholder="Type here your additional comments" v-model="feedbackInput.detail" />      
  </wizard-step>  

   <div class="buttons"
      v-if="isStep5Completed == true">
      <div class="btn" @click="post()">Yes, I would like to send the report</div>
      <div class="btn">No</div>
  </div>
  </div>
    <div class="msgifSent" v-if="errorReport == true">
        <div class="subTitle">
          Your (dummy) report has been sent
        </div>
        <p>
          Check your inbox for further commmunication.
        </p>
        <div class="newErrorReport" @click="newError()">Create new report</div>
    </div>
  </div>
</template>

<script>
import WizardStep from "./WizardStep";
import Question from "./Question";
import Solutions from "./Solutions";
import Screenshot from "./Screenshot";
import DatePicker from 'vue2-datepicker';
import 'vue2-datepicker/index.css';

export default {
  name: 'FeedbackWizard',
  components: {
      WizardStep,
      DatePicker,
      Question,
      Solutions,
      Screenshot,            
  },
  data() {
    return {
      active_el: 0,   
      time1: null, 
      time2: null, 
      time3: null,     
      title1: "Problem description",
      title2: "Questions",
      title3: "Possible solutions",
      title4: "Screenshot",
      title5: "Send report",
      title6: "Comments",
      subTitle1: "Describe the problem you're experiencing with your system. Please be as detailed as possible.",
      subTitle2: "Answer the following questions",
      subTitle3: "Possible solutions",
      subTitle4: "(optional) Screenshot",
      subTitle5: "Report error",
      subTitle6: "(optional) Additional comments", 
      stepCount3: 3,  
      errorReport: false,
      step1: 1, 
      step2: 2, 
      step3: 3, 
      step4: 4, 
      step5: 5, 
      step6: 6,                  
      currentStep: 1,
      isStep1Completed: false,
      isQuestion1Completed: false,
      isQuestion2Completed: false,
      isQuestion3Completed: false,
      isQuestion4Completed: false,                  
      isStep2Completed: false,
      isStep3Completed: false,
      isStep4Completed: false,
      isStep5Completed: false,
      isStep6Completed: false,                              
      problemDescription: { name: 'Problem description', detail: '', edit: false },
      questionOne: { name: 'When do you estimate you have first experienced the problem', detail: '', edit: false },
      questionThree: {name: 'Howw often do you think the problem was experienced?', detail: '', edit: false},
      questionFour: {name: 'What is the page URL?', detail: '', edit: false},
      feedbackInput: {name: 'Additional comments', detail: '', edit: false},            
      collectedData: {name: 'Captured information', detail: '', edit: false},
      questionTitle1: "When do you estimate you have first experienced the problem?",
      questionTitle2: "Is it a repeating problem?",
      questionTitle3: "Can you tell more about how often the problem was experienced?",      
      questionTitle4: "What is the page URL?",
      questionStep1: 1,
      questionStep2: 2,
      questionStep3: 3,      
      questionStep4: 4,
      form: []
  }},
    watch: {
        problemDescription: {
          deep: true,
          handler: function () { 
            if(this.problemDescription.detail.length >= 50) {
              this.isStep1Completed = true;
            } else{
              this.isStep1Completed = false;
            }
          }
        }, questionOne: {
          deep: true,
          handler: function () { 
            if(this.questionOne.detail != "") {
              this.isQuestion1Completed = true;
            } else{
              this.isQuestion1Completed = false;
            }
          }
        }, questionThree: {
          deep: true,
          handler: function () { 
            if(this.questionThree.detail.length >= 3) {
              this.isQuestion3Completed = true;
            } else{
              this.isQuestion3Completed = false;
            }
          }
        }, questionFour: {
          deep: true,
          handler: function () { 
            if(this.questionFour.detail.length >= 3) {
              this.isQuestion4Completed = true;
              this.isStep2Completed = true;
            } else{
              this.isQuestion4Completed = false;
              this.isStep2Completed = true;
            }
          }
        } , feedbackInput: {
          deep: true,
          handler: function () { 
            if(this.feedbackInput.detail.length >= 3) {
              this.isStep6Completed = true;
            } else{
              this.isStep6Completed = false;
            }
          }
        }                        
    },
    created: function() {
      this.form = [
        this.problemDescription,
        this.questionOne,
        this.questionThree,
        this.questionFour,
        this.feedbackInput,
      ];
    }, methods: {
        newError: function(){
          location.reload();
        },
        toggleEdit: function(index, item){
        item.edit = !item.edit;

        var vm = this;
        if (item.edit){
                vm.$refs.input[index].focus();
        }
    },

    saveEdit: function(index, item){
        this.toggleEdit(index, item);
    }, 
      delayStepFive: function () { 
        if (this.timer) {
            clearTimeout(this.timer);
            this.timer = null;
        }
        this.timer = setTimeout(() => {
            this.isStep5Completed = true;
        }, 3000);   
      },
      activate:function(el){
        this.active_el = el;
        if(this.active_el == 4){
          this.collectData();
        }
      },
      collectData() {
      var nAgt = navigator.userAgent;
      var browserName  = navigator.appName;
      var fullVersion  = ''+parseFloat(navigator.appVersion); 
      var majorVersion = parseInt(navigator.appVersion,10);
      var nameOffset,verOffset,ix;
      var OSName="Unknown OS";

      if ((verOffset=nAgt.indexOf("Opera"))!=-1) {
        browserName = "Opera";
        fullVersion = nAgt.substring(verOffset+6);
      if ((verOffset=nAgt.indexOf("Version"))!=-1) 
        fullVersion = nAgt.substring(verOffset+8);
      }
      else if ((verOffset=nAgt.indexOf("MSIE"))!=-1) {
        browserName = "Microsoft Internet Explorer";
        fullVersion = nAgt.substring(verOffset+5);
      }
      else if ((verOffset=nAgt.indexOf("Chrome"))!=-1) {
       browserName = "Chrome";
        fullVersion = nAgt.substring(verOffset+7);
      }
      else if ((verOffset=nAgt.indexOf("Safari"))!=-1) {
       browserName = "Safari";
       fullVersion = nAgt.substring(verOffset+7);
      if ((verOffset=nAgt.indexOf("Version"))!=-1) 
        fullVersion = nAgt.substring(verOffset+8);
      }
      else if ((verOffset=nAgt.indexOf("Firefox"))!=-1) {
        browserName = "Firefox";
        fullVersion = nAgt.substring(verOffset+8);
      }
      else if ( (nameOffset=nAgt.lastIndexOf(' ')+1) < 
                (verOffset=nAgt.lastIndexOf('/')) ) 
      {
        browserName = nAgt.substring(nameOffset,verOffset);
        fullVersion = nAgt.substring(verOffset+1);
      if (browserName.toLowerCase()==browserName.toUpperCase()) {
        browserName = navigator.appName;
      }
      }
      if ((ix=fullVersion.indexOf(";"))!=-1)
        fullVersion=fullVersion.substring(0,ix);
      if ((ix=fullVersion.indexOf(" "))!=-1)
          fullVersion=fullVersion.substring(0,ix);

      majorVersion = parseInt(''+fullVersion,10);
      if (isNaN(majorVersion)) {
        fullVersion  = ''+parseFloat(navigator.appVersion); 
        majorVersion = parseInt(navigator.appVersion,10);
      }

      if (navigator.appVersion.indexOf("Win")!=-1) OSName="Windows";
      if (navigator.appVersion.indexOf("Mac")!=-1) OSName="MacOS";
      if (navigator.appVersion.indexOf("X11")!=-1) OSName="UNIX";
      if (navigator.appVersion.indexOf("Linux")!=-1) OSName="Linux";
          
          this.form.push({name: 'Captured information', detail: "OS: " + OSName + ", Browser: " + browserName + ", Browser version: " + fullVersion + "."});
        }, uploadImg(){

          let imgurLink = 'https://www.ilac.com/wp-content/uploads/2019/06/placeholder-600x400.png';
          this.form.push({name: 'Screenshot of the page on which the problem is experienced', detail: imgurLink}); 
          this.isStep4Completed = true;
          this.delayStepFive();
        }, post(){
          this.errorReport = true;

      }
    }
}
</script>