<template>
  <div class="question-wrapper">

    <div class="questions">
      <div class="question" :class="{ 'completed' : completed}">
        <p><b>Question {{ step }} </b></p>
        <p>{{ title }} </p>
      </div>
      <div class="slotDiv" :class="{ 'completed' : completed}">
        <slot></slot>
      </div>      
      <hr>
    </div> 
  </div> 
</template>

<script>
export default {
  name: 'Question',
  props: {
    title,
    step,
    completed   
  }
}

let title = '', 
    step = '',
    completed = false;
</script>

<style>
.slotDiv.completed > div > div > input, .slotDiv.completed > input{
  background: #95d4cf;
  color: #1d263b;
  font-weight: bold;
  border: solid 1px #FFFFFF;
}
input.longerInput {
    width: 50%;
}
</style>
